import React from 'react';
import { withHandlers } from 'recompose';
import axios from '../axios';

const serializeForm = () => {
	const props = {};
	[...document.querySelectorAll('.add-form input, .add-form textarea')].forEach(
		input => {
			const name = input.getAttribute('id').split('.');
			if (name[1]) {
				if (!props[name[0]]) {
					props[name[0]] = {};
				}
				props[name[0]][name[1]] = input.value;
			} else {
				props[name[0]] = input.value;
			}
		}
	);
	return props;
};

const AddForm = ({ submitPattern }) => {
	return (
		<div className="add-form">
			<h2> </h2>
			<input
				className="add-form__field"
				type="text"
				placeholder=" (RUS)"
				id="title.ru"
			/>
			<textarea
				className="add-form__field"
				cols="30"
				rows="10"
				placeholder=" (RUS)"
				id="description.ru"
			/>
			<input
				className="add-form__field"
				type="text"
				placeholder=" (ENG)"
				id="title.en"
			/>
			<textarea
				className="add-form__field"
				cols="30"
				rows="10"
				placeholder=" (ENG)"
				id="description.en"
			/>
			<input
				className="add-form__field"
				type="text"
				placeholder=""
				id="pattern"
			/>
			<input
				className="add-form__field"
				type="text"
				placeholder=""
				id="placeholder"
			/>
			<input
				className="add-form__field"
				type="text"
				placeholder=""
				id="tags"
			/>
			<input
				className="add-form__field"
				type="text"
				placeholder=""
				id="nickname"
			/>
			<input
				className="add-form__field"
				type="number"
				placeholder=""
				id="prioritet"
			/>
			<div onClick={submitPattern} className="button button--green">
				
			</div>
		</div>
	);
};

export default withHandlers({
	submitPattern: () => () => {
		const postData = serializeForm();
		axios
			.post('http://5b3757f86223c40014605837.mockapi.io/new_patterns', postData)
			.then(() => {
				alert(' ');
			})
			.catch(() => alert('   '));
	}
})(AddForm);



